"use client";

import { useState } from 'react';
import { usePlanet } from '@/context/PlanetContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Separator } from '@/components/ui/separator';

export default function ParentalZone() {
  const { state, dispatch } = usePlanet();
  const [pinInput, setPinInput] = useState('');
  const [pinError, setPinError] = useState('');
  const [showPrintPreview, setShowPrintPreview] = useState(false);

  const handlePinSubmit = () => {
    if (pinInput === state.parentalPin) {
      dispatch({ type: 'UNLOCK_PARENTAL', payload: true });
      setPinError('');
      setPinInput('');
    } else {
      setPinError('Incorrect PIN. Please try again.');
      setPinInput('');
    }
  };

  const handleLogout = () => {
    dispatch({ type: 'UNLOCK_PARENTAL', payload: false });
  };

  const handleExportData = () => {
    const exportData = {
      planet: {
        landType: state.landType,
        skyType: state.skyType,
        seaType: state.seaType,
        features: state.features,
        weather: state.weather
      },
      creatures: state.creatures.map(c => ({
        name: c.name,
        power: c.power,
        color: c.color
      })),
      recordings: state.recordings.map(r => ({
        name: r.name,
        duration: r.duration
      })),
      story: state.story,
      createdAt: new Date().toISOString()
    };

    const dataStr = JSON.stringify(exportData, null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });
    const url = URL.createObjectURL(dataBlob);
    
    const link = document.createElement('a');
    link.href = url;
    link.download = `dream-world-${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  const handlePrintWorld = () => {
    setShowPrintPreview(true);
    setTimeout(() => {
      window.print();
      setShowPrintPreview(false);
    }, 500);
  };

  const resetPlanet = () => {
    if (window.confirm('Are you sure you want to reset the entire planet? This cannot be undone.')) {
      dispatch({ type: 'RESET_PLANET' });
    }
  };

  const formatDuration = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  if (!state.isParentalUnlocked) {
    return (
      <div className="space-y-6">
        <Card className="bg-white/90 backdrop-blur-sm">
          <CardHeader>
            <CardTitle className="text-lg text-center">Enter Parental PIN</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="text-center">
              <div className="w-16 h-16 bg-gray-200 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-2xl">üîí</span>
              </div>
              <p className="text-sm text-gray-600 mb-4">
                This area is for parents only. Please enter the PIN to continue.
              </p>
            </div>

            <div className="space-y-3">
              <Label htmlFor="pin">PIN Code</Label>
              <Input
                id="pin"
                type="password"
                value={pinInput}
                onChange={(e) => {
                  setPinInput(e.target.value);
                  setPinError('');
                }}
                placeholder="Enter 4-digit PIN"
                maxLength={4}
                className={pinError ? 'border-red-500' : ''}
                onKeyPress={(e) => {
                  if (e.key === 'Enter') {
                    handlePinSubmit();
                  }
                }}
              />
              
              {pinError && (
                <Alert className="bg-red-50 border-red-200">
                  <AlertDescription className="text-red-800">
                    {pinError}
                  </AlertDescription>
                </Alert>
              )}

              <Button onClick={handlePinSubmit} className="w-full" disabled={pinInput.length !== 4}>
                Access Parental Zone
              </Button>
            </div>

            <div className="text-center text-xs text-gray-500 mt-4">
              Demo PIN: 1234
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <Card className="bg-white/90 backdrop-blur-sm">
        <CardHeader>
          <div className="flex justify-between items-center">
            <CardTitle className="text-lg">Parental Dashboard</CardTitle>
            <Button variant="outline" size="sm" onClick={handleLogout}>
              Logout
            </Button>
          </div>
        </CardHeader>
      </Card>

      {/* World Summary */}
      <Card className="bg-white/90 backdrop-blur-sm">
        <CardHeader>
          <CardTitle className="text-lg">Child's World Summary</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Planet Configuration */}
          <div>
            <h4 className="font-semibold text-gray-800 mb-2">Planet Configuration</h4>
            <div className="grid grid-cols-2 gap-2">
              {state.landType && (
                <div className="flex items-center space-x-2">
                  <span className="text-sm text-gray-600">Land:</span>
                  <Badge variant="secondary">{state.landType}</Badge>
                </div>
              )}
              {state.skyType && (
                <div className="flex items-center space-x-2">
                  <span className="text-sm text-gray-600">Sky:</span>
                  <Badge variant="secondary">{state.skyType}</Badge>
                </div>
              )}
              {state.seaType && (
                <div className="flex items-center space-x-2">
                  <span className="text-sm text-gray-600">Sea:</span>
                  <Badge variant="secondary">{state.seaType}</Badge>
                </div>
              )}
              <div className="flex items-center space-x-2">
                <span className="text-sm text-gray-600">Features:</span>
                <Badge variant="secondary">{state.features.length}</Badge>
              </div>
            </div>
          </div>

          <Separator />

          {/* Creatures */}
          <div>
            <h4 className="font-semibold text-gray-800 mb-2">
              Creatures Created ({state.creatures.length})
            </h4>
            {state.creatures.length > 0 ? (
              <div className="space-y-2">
                {state.creatures.map((creature) => (
                  <div key={creature.id} className="flex items-center justify-between p-2 bg-gray-50 rounded">
                    <div>
                      <span className="font-medium">{creature.name}</span>
                      <Badge variant="outline" className="ml-2 text-xs">
                        {creature.power}
                      </Badge>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <p className="text-sm text-gray-500">No creatures created yet</p>
            )}
          </div>

          <Separator />

          {/* Voice Recordings */}
          <div>
            <h4 className="font-semibold text-gray-800 mb-2">
              Voice Recordings ({state.recordings.length})
            </h4>
            {state.recordings.length > 0 ? (
              <div className="space-y-2">
                {state.recordings.map((recording) => (
                  <div key={recording.id} className="flex items-center justify-between p-2 bg-gray-50 rounded">
                    <span className="font-medium">{recording.name}</span>
                    <Badge variant="outline" className="text-xs">
                      {formatDuration(recording.duration)}
                    </Badge>
                  </div>
                ))}
              </div>
            ) : (
              <p className="text-sm text-gray-500">No recordings made yet</p>
            )}
          </div>

          <Separator />

          {/* Story */}
          <div>
            <h4 className="font-semibold text-gray-800 mb-2">Story</h4>
            {state.story ? (
              <div className="p-3 bg-gray-50 rounded">
                <p className="text-sm text-gray-700 line-clamp-3">
                  {state.story}
                </p>
                <Badge variant="outline" className="mt-2 text-xs">
                  {state.story.split(' ').length} words
                </Badge>
              </div>
            ) : (
              <p className="text-sm text-gray-500">No story written yet</p>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Actions */}
      <Card className="bg-white/90 backdrop-blur-sm">
        <CardHeader>
          <CardTitle className="text-lg">Parental Actions</CardTitle>
        </CardHeader>
        <CardContent className="space-y-3">
          <Button onClick={handleExportData} className="w-full" variant="outline">
            üìÅ Export World Data
          </Button>
          
          <Button onClick={handlePrintWorld} className="w-full" variant="outline">
            üñ®Ô∏è Print Child's World
          </Button>
          
          <Button onClick={resetPlanet} className="w-full" variant="destructive">
            üóëÔ∏è Reset Planet (Careful!)
          </Button>
        </CardContent>
      </Card>

      {/* Safety Information */}
      <Card className="bg-green-50 border-2 border-green-200">
        <CardContent className="p-4">
          <h3 className="font-bold text-green-800 mb-2">üõ°Ô∏è Safety Information</h3>
          <ul className="text-sm text-green-700 space-y-1">
            <li>‚Ä¢ All content is created locally on this device</li>
            <li>‚Ä¢ No personal information is shared online</li>
            <li>‚Ä¢ Voice recordings are stored locally only</li>
            <li>‚Ä¢ You can export or delete all data anytime</li>
            <li>‚Ä¢ The app works offline for privacy</li>
          </ul>
        </CardContent>
      </Card>

      {/* Usage Statistics */}
      <Card className="bg-blue-50 border-2 border-blue-200">
        <CardContent className="p-4">
          <h3 className="font-bold text-blue-800 mb-2">üìä Usage Statistics</h3>
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <span className="text-blue-700 font-medium">Total Creatures:</span>
              <span className="ml-2 text-blue-800">{state.creatures.length}</span>
            </div>
            <div>
              <span className="text-blue-700 font-medium">Recordings:</span>
              <span className="ml-2 text-blue-800">{state.recordings.length}</span>
            </div>
            <div>
              <span className="text-blue-700 font-medium">Story Words:</span>
              <span className="ml-2 text-blue-800">
                {state.story ? state.story.split(' ').length : 0}
              </span>
            </div>
            <div>
              <span className="text-blue-700 font-medium">Planet Features:</span>
              <span className="ml-2 text-blue-800">{state.features.length}</span>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Print Preview (Hidden) */}
      {showPrintPreview && (
        <div className="print:block hidden">
          <div className="p-8 bg-white">
            <h1 className="text-2xl font-bold mb-4">My Dream World</h1>
            <div className="space-y-4">
              <div>
                <h2 className="text-lg font-semibold">Planet Configuration</h2>
                <p>Land: {state.landType || 'Not set'}</p>
                <p>Sky: {state.skyType || 'Not set'}</p>
                <p>Sea: {state.seaType || 'Not set'}</p>
              </div>
              
              {state.creatures.length > 0 && (
                <div>
                  <h2 className="text-lg font-semibold">My Creatures</h2>
                  {state.creatures.map((creature) => (
                    <p key={creature.id}>
                      {creature.name} - Power: {creature.power}
                    </p>
                  ))}
                </div>
              )}
              
              {state.story && (
                <div>
                  <h2 className="text-lg font-semibold">My Story</h2>
                  <p className="whitespace-pre-wrap">{state.story}</p>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
